.card {
    height: 250px;
    width: 250px;
    float: left;
    margin: 20px;
    background-size: cover;
    border-width: 5px;
    border-style: solid;
    border-color: #00d9ff;
}

.card:hover {
    cursor: pointer;
    border-color: #ff4800;
}


